import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable()
export class TagService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }

  getTagList(param): Observable<any> {
    return this.http.get(`${this.API_URL}/tag`, { params: param });
  }

  addTag(data): Observable<any> {
    return this.http.post(`${this.API_URL}/tag`, data);
  }

  editTag(id, data): Observable<any> {
    return this.http.put(`${this.API_URL}/tag/${id}`, data);
  }

  getTagById(id, param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/tag/${id}`, { params: param });
  }

  getTagMasterData(): Observable<any> {
    return this.http.get(`${this.API_URL}/tag/master-data`);
  }

  // GenerateRfid(): Observable<any> {
  //   return this.http.get(`${this.API_URL}/generate-rfid`);
  // }




}
